// Default Style for TinyMCE available tags

.aligncenter, dl.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.alignleft {
  float: left; }

.alignright {
  float: right; }

.wp-caption {
  border: 1px solid #ddd;
  text-align: center;
  background-color: #f3f3f3;
  padding-top: 4px;
  margin: 10px 0; }

.wp-caption img {
  margin: 0;
  padding: 0;
  border: 0 none;
  -apple-user-drag: none;
  -khtml-user-drag: none;
  -webkit-user-drag: none; }

.wp-caption-dd {
  font-size: 11px;
  line-height: 17px;
  padding: 0 4px 5px;
  margin: 0; }

pre {
  font: 12px/18px Consolas, Monaco, monospace; }

td {
  color: #000;
  font-size: 11px;
  margin: 8px; }

strong {
  font-weight: strong; }

em, i {
  font-style: italic; }
