@mixin heroResponsiveHeights {
  @include mobile {
    height: 200px; }
  @include tablet {
    height: 320px; }
  @include desktop {
    height: 360px; }
  @include widescreen {
    height: 500px; }
  @include fullhd {
    height: 650px; } }

@mixin heroResponsiveHeightsAnwalt {
  @include mobile {
    height: 200px; }
  @include tablet {
    height: 320px; }
  @include desktop {
    height: 350px; }
  @include widescreen {
    height: 500px; }
  @include fullhd {
    // height: 650px
    height: 825px; } }

@mixin headerHeights {
  height: 56px !important;
  @include tablet {
    height: 70px !important; }
  @include desktop {
    height: 80px !important; }
  @include widescreen {
    height: 112px !important; }
  @include fullhd {
    height: 112px !important; } }

@mixin headerHeightsBreaking {
  height: 116px !important;
  @include tablet {
    height: 130px !important; }
  @include desktop {
    height: 140px !important; }
  @include widescreen {
    height: 172px !important; }
  @include fullhd {
    height: 172px !important; } }

@mixin headerInnerPadding {
  padding: 0 1rem !important;
  @include tablet {
    padding: 0 2rem !important; }
  @include desktop {
    padding: 0 2.5rem !important; }
  @include widescreen {
    padding: 0 3rem !important; }
  @include fullhd {
    padding: 0 3.3rem !important; } }
