.is-darkblue {
  background: $darkblue; }
.is-red {
  background: $red; }
.is-turquoise {
  background: $turquoise; }
.is-lightgrey {
  background: $lightgrey; }
.is-darkgrey {
  background: $darkgrey; }
.is-verylightgrey {
  background: $verylightgrey; }

html, body {
  width: 100vw!important;
  overflow-x: hidden;
  overflow-y: auto; }

html {
  background: $darkerblue; }
body {
  margin: 0 !important;
  padding: 0 !important;
  background: white;
  font-family: 'Din', sans-serif;
  line-height: 1.5;
  font-size: 16px;
  color: $darkgrey;
  @include tablet {
    font-size: 18px !important; }
  @include desktop {
    font-size: 18px !important; }
  @include widescreen {
    font-size: 18px !important; }
  @include fullhd {
    font-size: 20px !important; } }

p {
  margin-bottom: 1em; }

h2 {
  font-size: 1.5rem;
  line-height: 1.3; }

h2.section-title {
  font-size: 1.5rem;
  margin-bottom: 1em;
  font-weight: normal;
  text-align: center; }

.title {
  color: $darkgrey; }

.button {
  padding: .5em 2.75em!important;
  border-radius: 0!important;
  height: 2.75em!important;
  background: none;
  border: 1px solid $lightgrey;
  &:hover {
    background: $verylightgrey;
    color: $darkgrey; }
  &:focus {
    outline: 0; } }
.shade {
  display: block;
  margin-top: -3rem;
  height: 3em;
  background: linear-gradient(180deg,hsla(0,0%,100%,0) 0,#fff);
  width: 100%;
  position: relative;
  z-index: 4;
  margin-bottom: 1em; }

// --- SHARED PARTIALS & HELPERS

.reverse-column-order-on-mobile {
  @include mobile {
    display: flex;
    flex-direction: column-reverse; } }

section.awards {
  padding: 2em 0 5em; }

.awards {
  text-align: center;
  transition: all 300ms ease-in-out;
  .award-items {
    overflow: hidden;
    height: auto; }
  .closed {
    transition: all 300ms ease-in-out;
    overflow: hidden; }
  .button {
    margin-top: 2em;
    transition: all 200ms;
    transform: scale(1); }
  .award {
    transition: all 300ms ease;
    transform: translateY(0);
    opacity: 1;
    position: relative;
    &.hide {
      position: absolute;
      transform: translateY(2em);
      opacity: 0; } } }


#gallery-1 img {
  border: none!important; }

// Newsletter / Job Alert
.job-alert, .newsletter {
  margin: 4em 0;
  text-align: center;
  background: $verylightgrey;
  .bell-icon {
    width: 2em;
    // margin: 0 auto 0
    transition: all 150ms ease-in-out;
    &:hover {
      transform: rotate(-15deg) scale(1.15); } }
  .boxy {
    // border: 8px solid #f0f0f0
    padding: 2em;
    border-radius: .5em; } }

// BG Boxy Helper
.bg-boxy {
  // height: 200px
  background-size: cover;
  background-position: center center;
  background-color: #f7f7f7; }

$gridpad: 0.5em;
.aspect-outer {
  position: relative;
  &:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: (3 / 5) * 100%; }
  > .aspect-inner {
    position: absolute;
    top: $gridpad;
    right: $gridpad;
    bottom: $gridpad;
    left: $gridpad; } }

.aspect-outer.square {
  &:before {
    padding-top: (1 / 1) * 100%; } }

.is-paddingless {
  .aspect-outer {
    > .aspect-inner {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0; } } }

.linkedin-item {
  position: relative;
  &:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: (1 / 1) * 100%; }
  > div {
    position: absolute;
    top: $gridpad;
    right: $gridpad;
    bottom: $gridpad;
    left: $gridpad;
    background: $verylightgrey;
    padding: 0.75em 1.5em;
    overflow-x: hidden;
    overflow-y: auto; } }


.bg-contain {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat; }

// FORM FIXES

.nf-form-content hr.ninja-forms-field {
  margin: 2em 0 3em; }

.nf-form-fields-required {
  color: $lightgrey;
  font-size: .8em; }

.nf-form-content button, .nf-form-content input[type=button], .nf-form-content input[type=submit] {
